<template><section><template>
<blockquote>
<p>Radio 单选框</p>
</blockquote>
  <br> 
<h4>由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</h4>
<p><br><br></p>
<h4><strong>基础用法:</strong></h4>
<p><br><br></p>
<template>
    <ga-radio :active="true" label="radio"></ga-radio>
    <ga-radio :active="false">单选框</ga-radio>
</template>
<p><br><br></p>
<pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">:active</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"radio"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">:active</span>=<span class="hljs-string">"false"</span>&gt;</span>单选框<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<p><br><br></p>
<h1>参数</h1>
  <br>
<table>
<thead>
<tr>
<th>参数</th>
<th>类型</th>
<th>默认值</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>String</td>
<td>default</td>
<td><code v-pre="">primary</code>, <code v-pre="">success</code>, <code v-pre="">info</code>, <code v-pre="">warning</code>,<code v-pre="">danger</code></td>
</tr>
<tr>
<td>plain</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">true/false</code></td>
</tr>
<tr>
<td>size</td>
<td>String</td>
<td>small</td>
<td><code v-pre="">mini</code>,<code v-pre="">small</code>,<code v-pre="">medium</code></td>
</tr>
<tr>
<td>click</td>
<td>Event</td>
<td>-</td>
<td>按钮点击事件</td>
</tr>
</tbody>
</table>
</template>


</section></template>
<style lang="scss">
    .ga-radio{
        padding-right:12px;
    }
</style>
<script>
  export default {
    data () {
      return {
          radioData:null,
      }
    },
    mounted(){
        this.$on('input',(val)=>{
            this.radioData = val;
        })
    },
    update(){
        console.log(this.radioData);
    }
  }
</script>


